@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
    margin: 0;
    font-family: "Source Sans Pro", -apple-system, sans-serif;
    background-color: #121212;
    color: #ffffff;
}
.main__class {
    height: 100vh;
    display: flex;
    background-color: #121212;
}
.home {
    width: 100%;
    display: flex;
    height: 100vh;
}
.fullWidth > label {
    width: 100%;
}
.fullWidth {
    display: flex;
    align-items: center;
}
.fullWidth > button {
    margin-top: 0.8rem;
}
.chats {
    width: 70%;
    display: flex;
}
.chats > div {
    width: 50%;
}
.logs {
    width: 30%;
    background-color: red;
}
canvas {
    border-radius: 50%;
}
.hideQR > div > button {
    display: none;
}
.hideQR > input {
    padding-right: 3rem;
}
@media only screen and (max-width: 920px) {
    .logs {
        display: none;
    }
    .chats {
        width: 100%;
    }
}

